.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.contactinfo {
  margin-top: 100px;
  margin-bottom: 50px;
}

@media screen and (max-width: 960px) {
  .contactinfo {
    margin-top: -50px;
  }
}

.navbarri {
  margin-right: 500px;
}

@media screen and (max-width: 960px) {
  .navbarri {
    margin-right: calc(100% * 0.8);
  }
}
@media screen and (max-width: 690px) {
  .navbarri {
    margin-right: calc(100% * 0.75);
  }
}
@media screen and (max-width: 520px) {
  .navbarri {
    margin-right: calc(100% * 0.7);
  }
}
@media screen and (max-width: 430px) {
  .navbarri {
    margin-right: calc(100% * 0.6);
  }
}
@media screen and (max-width: 330px) {
  .navbarri {
    margin-right: calc(100% * 0.5);
  }
}
@media screen and (max-width: 290px) {
  .navbarri {
    margin-right: calc(100% * 0.45);
  }
}
.headerbar {
  margin: auto;
}
@media screen and (max-width: 960px) {
  .headerbar {
    margin: 0;
  }
}

.headerlink {
  margin-left: auto;
}

@media screen and (max-width: 1042px) {
  .headerlink {
    margin-left: -80px;
  }
}

.formpadding {
  padding: 1px 80px;
  padding-bottom: 80px;
}
@media screen and (max-width: 500px) {
  .formpadding {
    padding: 1px 30px;
  }
}

.toolstip {
  position: absolute;
  display: inline-block;
  height: 52px;
  border-radius: 4px;
  background-color: #f4f7ff;
}
.toolstipmain {
  position: relative;
  margin-left: 70px;
  margin-bottom: 80px;
}
.toolstiplogo {
  margin-left: -22px;
  width: 44px;
  height: 44px;
  box-shadow: 5px 2px 18px 0 #d5dcf0;
  background-color: #fff;
  color: #fafafa;
  display: flex;
  overflow: hidden;
  position: relative;
  font-size: 1.25rem;
  align-items: center;
  flex-shrink: 0;
  font-family: Poppins;
  line-height: 1;
  user-select: none;
  border-radius: 50%;
  justify-content: center;
}
.tooltipimg {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100%;
  margin-bottom: 10px;
}
@media screen and (max-width: 960px) {
  .toolstip {
    position: initial;
    display: list-item;
  }
  .toolstipmain {
    margin-left: 0px;
    margin-bottom: 0;
    list-style: none;
  }
  .toolstiplogo {
    margin-left: 10px;
    width: 40px;
    height: 40px;
  }
  .tooltipimg {
    width: 50%;
    height: 50%;
  }
}

.dotbox {
  border: dotted 7px #6e88a9;
  position: initial;
  margin: -728px auto auto;
  height: 100%;
  width: 60%;
  border-radius: 25.5px;
  overflow: hidden;
}

.heading {
  margin: 0.5em 0;
  font-size: 2.5em;
  font-weight: 700;
  line-height: 1.25;
  color: #1a325f;
}
@media screen and (max-width: 680px) {
  .heading {
    font-size: 2em;
  }
}
@media screen and (max-width: 360px) {
  .heading {
    font-size: 1.5em;
  }
}
